nav {
    max-width: 40rem;
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    bottom: 3rem;
    background: transparent;
}
.nav-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    height: 2rem;
    transition: var(--transition);
    border: solid 1px;
    border-radius: 22px;
    padding: 1.4rem 2rem;
    overflow: hidden;
}
.nav-list > * {
    background: transparent;
}
.link {
    display: flex;
    flex-direction: column;
    align-items: center;
    transform: translateY(0.8rem);
    gap: 0.5rem;
    transition: var(--transition);
}
.link > * {
    background: transparent;
    font-size: 1.8rem;
    color: var(--d-color);
    transition: var(--transition);
}
.link > h6 {
    opacity: 0;
    font-size: 1rem;
    transition: var(--transition);
}
.nav-list:hover .link > h6 {
    opacity: 1;
}
.nav-list:hover {
    height: 4rem;
    transition-delay: 1s;
}
.nav-list:hover .link {
    transform: translateY(0rem);
    transition-delay: 1s;
}
@media (max-width: 820px) {
    nav {
        margin: 0 2rem;
    }
}
@media (max-width: 590px) {
    .link > * {
        font-size: 1rem;
    }
    .nav-list {
        gap: 0;
    }

}