.main-container {
    width: 80%;
    margin: auto;
}
.top-headers {
    width: 60%;
    gap: 6rem;
    margin: auto;
    display: flex;
    align-items: center;
    height: 15rem;
}
.actual-temp {
    font-size: 8rem;
    position: relative;
    width: 18rem;
    margin-right: 5rem;
}
.c-f-btn {
    position: absolute;
    right: -3rem;
    top: 1rem;
    font-size: 1.3rem;
    border: solid 1px;
    padding: 2px;
    border-radius: 5px;
    cursor: pointer;
    transition: var(--transition);
}
.c-f-btn:hover{
    background-color: var(--alt-bg);
    opacity: .8;
}
.pictoral-description {
    width: 9rem;
    margin-bottom: 1rem;
    animation: cloud 2s ease-in-out infinite alternate;
}
.description {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 2rem;
    text-transform: capitalize;
}
@keyframes cloud {
    from {
        transform: translateY(1rem);
    }
    to{
        transform: translateY(0rem);
    }
}
.city-name {
    font-size: 7rem; 
}
.location-info {
    text-align: center;
}
.details {
    text-align: left;
    width: 50%;
    margin: auto;
    margin-top: 5rem;
    font-size: 1.4rem;
    line-height: 3rem;
}
.detail-images {
    width: 1rem;
    color: var(--d-color);
}
.search-holder {
    position: absolute;
    top: 3rem;
    right: 10rem;
    border: solid 1px ;
    display: flex;
    align-items: center;
    padding: 10px 10px 0 1rem;
    border-radius: 1rem;
}
.search-city-icn {
    font-size: 2rem;
    cursor: pointer;
}
.inp-city {
    font-size: 1.3rem;
    transition: var(--transition);
    width: 0;
}
.search-holder:hover .inp-city {
    width: 12rem;
}
.inp-city:focus {
    width: 12rem;
}
