    top: 3.5rem;
    left: 0;
    width: 8rem;
} */
@media (max-width: 820px) {
    .city-name {
        font-size: 5rem;
    }
    .description {
        font-size: 1.5rem;
    }
    .actual-temp{
        font-size: 6rem;
    }
    .top-headers{
        gap: 2rem;
        margin-bottom: 3rem;
    }
    .details{
        font-size: 1.2rem;
    }
}
@media (max-width: 590px) {
    .city-name {
        font-size: 3rem;
    }
    .description {
        font-size: 1rem;
    }
    .actual-temp{
        font-size: 3rem;
    }
    .pictoral-description{
        width: 6rem;
    }
    .top-headers{
        gap: 2rem;
        margin-bottom: 1rem;
    }
    .details{
        font-size: 0.8rem;
        width: 90%;
        margin: 6rem auto;
    }
}